import React, {useContext, useEffect} from 'react'
import { AudioContext } from ".././context/index"
import useImage from '.././hooks/useImage'
import {Howl, Howler} from 'howler';

const SecondLesson = () => {
  
  
  const { interval_id, timeout_id } = useContext(AudioContext);
  const { loading, error, image } = useImage('eye.svg')
  

  var sound123 = new Howl({
    src: ['audio.mp3'],
    loop: false,
    volume: 0.5,
  });


  function timeout(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }
 

  async function letsgo1(){
    timeout_id.make(()=>sound123.play(),30100)
    timeout_id.make(()=>sound123.play(),30000)
    timeout_id.make(()=>sound123.play(),30200)
    timeout_id.make(()=>sound123.play(),1250)
    await timeout(1250)
    interval_id.make(()=>sound123.play(),3000)
    await timeout(60000)
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
  }
  
  useEffect(() => {
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    console.log(22222);
    letsgo1()
  }, [] );


  return (
    <div className='wrapImageImage2 wrapImageImage'>
      {/* <button onClick={()=>interval_id.clearAll()}>clear</button> */}
      <div className='wrapImage wrapImage2'>
        <img 
        className='imageSecondLesson'
        src={image} alt="" />
      </div>
    </div>
  )
}

export default SecondLesson