import React, {useContext, useEffect} from 'react'
import { AudioContext } from ".././context/index"
import useImage from '.././hooks/useImage'
import {Howl, Howler} from 'howler';

const SeventhElement = () => {

  const { interval_id, timeout_id } = useContext(AudioContext);
  const { loading:loading1, error:error1, image: image1 } = useImage('finger.svg')
  const { loading:loading2, error:error2, image: image2 } = useImage('pencil.svg')

  var sound123 = new Howl({
    src: ['audio.mp3'],
    loop: false,
    volume: 0.5,
  });


  function timeout(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }
 

  async function letsgo1(){
    timeout_id.make(()=>sound123.play(),1000)
    // timeout_id.make(()=>sound123.play(),4400)
    await timeout(1000)
    interval_id.make(()=>sound123.play(),1000)
    await timeout(38000)
    interval_id.clearAll()
    interval_id.clearAll()
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
  }
  
  useEffect(() => {
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    letsgo1()
    console.log(77777);
  }, [] );

  return (
    <div className='wrapImageImage'>
      <div className='wrapImage7'>
        <img 
        className='wrapImage71'
        src={image1} alt="" />
        <img 
        className='wrapImage72'
        src={image2} alt="" />
      </div>
    </div>
  )
}

export default SeventhElement