import React from 'react'
import { Link } from 'react-router-dom'

const Navbar = () => {
  return (
    <div className='Navbar'>
        <Link to='/' className='homeButton'>
            <button>
                Home
            </button>
        </Link>
        <Link to='/first'>
            <button>
                1
            </button>
        </Link>
        <Link to='/second'>
            <button>
                2
            </button>
        </Link>
        <Link to='/third'>
            <button>
                3
            </button>
        </Link>
        <Link to='/fouth'>
            <button>
                4
            </button>
        </Link>
        <Link to='/fifth'>
            <button>
                5
            </button>
        </Link>
        <Link to='/sixth'>
            <button>
                6
            </button>
        </Link>
        <Link to='/seventh'>
            <button>
                7
            </button>
        </Link>
    </div>
  )
}

export default Navbar