import React, {useContext, useEffect} from 'react'
import useImage from '.././hooks/useImage'
import {Howl, Howler} from 'howler';
import { useLocation } from "react-router-dom";
import { AudioContext } from ".././context/index"


const FirstLesson = () => {


  const { interval_id, timeout_id } = useContext(AudioContext);
  const { loading, error, image } = useImage('eye.svg')
  

  var sound123 = new Howl({
    src: ['audio.mp3'],
    loop: false,
    volume: 0.5,
  });


  function timeout(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }
 

  async function letsgo1(){
    timeout_id.make(()=>sound123.play(),1250)
    // setTimeout(()=>sound123.play(),1250)
    await timeout(1250)
    interval_id.make(()=>sound123.play(),2500)
    await timeout(50000)
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
  }
  
  useEffect(() => {
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    letsgo1()
    console.log(11111);
  }, [] );
  

  

  return (
    <div className='wrapImageImage'>
      {/* <button onClick={()=>interval_id.clearAll()}>clear</button> */}
      <div className='wrapImage wrapImage1'>
        <img src={image} alt="" />
      </div>
    </div>
  )
}

export default FirstLesson