import React, {useContext, useEffect} from 'react'
import { AudioContext } from ".././context/index"
import useImage from '.././hooks/useImage'
import {Howl, Howler} from 'howler';

const ThirdLesson = () => {
  const { interval_id, timeout_id } = useContext(AudioContext);
  const { loading, error, image } = useImage('eye.svg')
  

  var sound123 = new Howl({
    src: ['audio.mp3'],
    loop: false,
    volume: 0.5,
  });


  function timeout(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }
 

  async function letsgo1(){
    timeout_id.make(()=>sound123.play(),3000)
    timeout_id.make(()=>sound123.play(),5000)
    await timeout(3000)
    interval_id.make(()=>sound123.play(),5000)
    await timeout(2000)
    interval_id.make(()=>sound123.play(),5000)
    await timeout(48000)
    interval_id.clearAll()
    interval_id.clearAll()
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
  }
  
  useEffect(() => {
    interval_id.clearAll()
    interval_id.clearAll()
    timeout_id.clearAll()
    timeout_id.clearAll()
    letsgo1()
    console.log(333333);
  }, [] );


  return (
    <div className='wrapImageImage'>
      <div className='wrapImage wrapImage3'>
        <img src={image} alt="" />
      </div>
    </div>
  )
}

export default ThirdLesson