import logo from './logo.svg';
import './App.css';
import FirstLesson from './components/FirstLesson';
import { HashRouter, Route, Routes } from 'react-router-dom';
import SecondLesson from './components/SecondLesson';
import ThirdLesson from './components/ThirdLesson';
import Home from './components/Home';
import Navbar from './components/Navbar';
import FouthLesson from './components/FouthLesson';
import FifthLesson from './components/FifthLesson';
import SixthElement from './components/SixthElement';
import SeventhElement from './components/SeventhElement';
import { createContext, useEffect } from 'react';
import { AudioContext } from "./context/index";
import {Howl, Howler} from 'howler';
// import mp3 from '.././build/assets/audio/Big_O_Opening_US_Version.mp3';

function App() {
  const audioTune = new Audio('.././audio/audio.mp3')



  var interval_id = {
    // to keep a reference to all the intervals
    intervals : new Set(),
    
    // create another interval
    make(...args) {
        var newInterval = setInterval(...args);
        this.intervals.add(newInterval);
        return newInterval;
    },

    // clear a single interval
    clear(id) {
        this.intervals.delete(id);
        return clearInterval(id);
    },

    // clear all intervals
    clearAll() {
        for (var id of this.intervals) {
            this.clear(id);
        }
    }
};

  var timeout_id = {
    // to keep a reference to all the intervals
    timeout : new Set(),
    
    // create another interval
    make(...args) {
        var newTimeout = setTimeout(...args);
        this.timeout.add(newTimeout);
        return newTimeout;
    },

    // clear a single interval
    clear(id) {
        this.timeout.delete(id);
        return clearTimeout(id);
    },

    // clear all intervals
    clearAll() {
        for (var id of this.timeout) {
            this.clear(id);
        }
    }
};


  useEffect(()=>{
    // audioTune.play();
  }, [])
  return (
    <HashRouter>
      <AudioContext.Provider
      value={{
        audioTune,
        interval_id,
        timeout_id
      }}
      >
        <div className="App">
          <Navbar/>
          <div>
            <Routes>
              <Route path='/first' element={<FirstLesson/>}/>
              <Route path='/second' element={<SecondLesson/>}/>
              <Route path='/third' element={<ThirdLesson/>}/>
              <Route path='/fouth' element={<FouthLesson/>}/>
              <Route path='/fifth' element={<FifthLesson/>}/>
              <Route path='/sixth' element={<SixthElement/>}/>
              <Route path='/seventh' element={<SeventhElement/>}/>
              <Route path='/' element={<Home/>}/>
              <Route path='*' element={<Home/>}/>
            </Routes>
          </div>
          {/* <FirstLesson/> */}
          {/* <SecondLesson/>
          <ThirdLesson/> */}
        </div>
      </AudioContext.Provider>
    </HashRouter>
  );
}

export default App;
